/* 구글 웹 폰트 연결 */
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap");

/* 공통 */
:root {
  --main-bg-color: #1864ab;
  --box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
  --font-family: "Noto Sans KR", sans-serif;
}
body {
  background-color: #f8f9fa;
}

.App {
  font-family: var(--font-family);
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  overflow: hidden;
  word-break: keep-all;
}

.btn {
  font-family: var(--font-family);
  padding: 0.5em 1em;
  border: none;
  border-radius: 4em;
  font-size: 1em;
  color: #eee;
  text-align: center;
  cursor: pointer;
  box-shadow: var(--box-shadow);
}

.submitBtn {
  background-color: var(--main-bg-color);
  font-weight: 900;
}
.sortBtn {
  background-color: #5f3dc4;
}
.closeBtn {
  background-color: #c92a2a;
}

/* headerSection */
.black-nav {
  display: flex;
  width: 100%;
  padding-left: 2em;
  justify-content: flex-start;
  background-color: var(--main-bg-color);
  color: #eee;
  box-shadow: var(--box-shadow);
}

/* listSection */
.listSection {
  display: flex;
  flex-direction: column;
  margin-top: 2em;
  padding: 0 2em;
  color: #343a40;
}
.list {
  padding: 1.25em 0 1.5em;
  border-bottom: 1px solid #868e96;
}
.list:first-child {
  padding-top: 0;
}
.editBtn,
.deleteBtn {
  cursor: pointer;
  font-size: 1.17em;
  border: 0;
  background-color: transparent;
}

/* inputSection */
.inputSection {
  display: flex;
  justify-content: center;
  margin-top: 2em;
  padding: 0 2em;
}
.inputSection input[type="text"] {
  flex-grow: 1;
  height: auto;
  line-height: normal;
  padding: 0.25em 0.5em 0.25em 1em;
  margin-right: 0.5em;
  border: 2px solid #ced4da;
  background-color: #f8f9fa;
  border-radius: 4em;
  outline: none;
  caret-color: var(--main-bg-color);
  font-size: 1em;
  box-shadow: var(--box-shadow);
}
.inputSection input[type="text"]:focus {
  border: 2px solid var(--main-bg-color);
}

/* sortSection */
.sortSection {
  display: flex;
  justify-content: flex-start;
  margin-top: 2em;
  padding: 0 2em;
}

/* modalSection */
.modal {
  display: flex;
  flex-direction: column;
  background-color: #eee;
  margin: 2em;
  padding: 2em;
  border-radius: 0.5em;
  box-shadow: var(--box-shadow);
}

.modal h4 {
  margin-top: 0;
}

/* 미디어쿼리 */
@media screen and (max-width: 320px) {
  .inputSection {
    padding: 0 1em;
  }
}
